import React, { useEffect, useState } from "react";
import { getDataTable, convertToJSON } from "../../js/excelFunction.js";
import Table from "../ui/Table.js";

const Evaluation = () => {
  const [data, setData] = useState([]);
  const [account, setAccount] = useState("");
  const [groupedTotals, setGroupedTotals] = useState([]);
  const [error, setError] = useState(""); // Them trang thai error

  useEffect(() => {
    // Fetch data from an API or other source
    const fetchData = async () => {
      try {
        const response = await getDataTable("WBS", "TblWbs");
        const jsonData = convertToJSON(response);
        let filteredData = jsonData;
        if (account && account.trim() !== "") {
          filteredData = jsonData.filter((item) => item.PIC.toLowerCase().includes(account.toLowerCase()));
        }
        const updatedData = calculateEVM(filteredData);
        setData(updatedData);
      } catch (error) {
        setError(error);
        console.error("Error fetching performance data:", error);
      }
    };
    fetchData();
  }, [account]);

  const calculateEVM = (data) => {
    const totalsByPIC = {};
    const updatedData = data
      .filter((item) => item["fPoint"] !== 0 && item["fPoint"] !== "")
      .map((item) => {
        let planComplete;
        let performance;
        let PV;
        let EV;
        let AC;
        let SPI;
        let CPI;
        let SV;
        let CV;

        if (item["P.Effort"] === "") {
          item["P.Effort"] = 0;
        }

        if (item["A.Effort"] === "") {
          item["A.Effort"] = 0;
        }

        if (item["fPoint"] === "") {
          item["fPoint"] = 0;
        }

        if (item["P.Effort"] === 0 && item["A.Effort"] > 0) {
          item["P.Effort"] = item["A.Effort"];
        }
        if (item["P.Effort"] !== 0) {
          planComplete = (item["A.Effort"] / item["P.Effort"]) * 0.8;
        } else {
          planComplete = 0;
        }

        if (item["%A.Completed"] < planComplete) {
          performance = "(" + ((item["%A.Completed"] - planComplete) * 100).toFixed(0) + "%)";
        } else if (item["%A.Completed"] === planComplete && planComplete > 0) {
          performance = "";
        } else {
          performance = "(" + ((item["%A.Completed"] - planComplete) * 100).toFixed(0) + "%)";
        }

        PV = calculatePV(item);
        EV = calculateEV(item);
        AC = calculateAC(item);
        SPI = calculateSPI(PV, EV);
        CPI = calculateCPI(AC, EV);
        SV = calculateSV(PV, EV);
        CV = calculateCV(AC, EV);

        item["%A.Completed"] = (item["%A.Completed"] * 100).toFixed(0);
        if (!totalsByPIC[item["PIC"]]) {
          totalsByPIC[item["PIC"]] = { totalFpoint: 0, totalPV: 0, totalEV: 0, totalAC: 0 };
        }
        totalsByPIC[item["PIC"]].totalFpoint += item["fPoint"];
        totalsByPIC[item["PIC"]].totalPV += PV;
        totalsByPIC[item["PIC"]].totalEV += EV;
        totalsByPIC[item["PIC"]].totalAC += AC;

        return {
          ...item,
          PV: PV.toFixed(1),
          EV: EV.toFixed(1),
          AC: AC.toFixed(1),
          SPI: SPI,
          CPI: CPI,
          SV: SV,
          CV: CV,
          planComplete: (planComplete * 100).toFixed(0),
          performance: performance,
        };
      });
    const groupedTotalsArray = Object.keys(totalsByPIC).map((PIC) => ({
      PIC,
      totalFpoint: totalsByPIC[PIC].totalFpoint.toFixed(1),
      totalPV: totalsByPIC[PIC].totalPV.toFixed(1),
      totalEV: totalsByPIC[PIC].totalEV.toFixed(1),
      totalAC: totalsByPIC[PIC].totalAC.toFixed(1),
    }));

    setGroupedTotals(groupedTotalsArray);

    return updatedData;
  };

  const calculatePV = (item) => {
    if (item["A.Effort"] === 0 || item["P.Effort"] === 0) {
      return 0;
    }
    if (item["A.Effort"] > item["P.Effort"]) {
      return item["fPoint"];
    } else {
      return (item["A.Effort"] / item["P.Effort"]) * item["fPoint"];
    }
  };

  const calculateEV = (item) => {
    return item["%A.Completed"] * item["fPoint"];
  };

  const calculateAC = (item) => {
    if (item["A.Effort"] === 0 || item["P.Effort"] === 0) {
      return 0;
    }
    return (item["A.Effort"] / item["P.Effort"]) * item["fPoint"];
  };

  const calculateSPI = (PV, EV) => {
    return PV === 0 ? 1.0 : (EV / PV).toFixed(2);
  };

  const calculateCPI = (AC, EV) => {
    return AC === 0 ? 1.0 : (EV / AC).toFixed(2);
  };

  const calculateSV = (PV, EV) => {
    return (EV - PV).toFixed(2);
  };

  const calculateCV = (AC, EV) => {
    return (EV - AC).toFixed(2);
  };

  const totalColumns = [
    { header: "PIC", key: "PIC" },
    { header: "Total Fpoint", key: "totalFpoint" },
    { header: "Total PV", key: "totalPV" },
    { header: "Total EV", key: "totalEV" },
    { header: "Total AC", key: "totalAC" },
  ];
  const columns = [
    { header: "TaskID", key: "TaskID" },
    { header: "PIC", key: "PIC" },
    { header: "Fpoint", key: "fPoint" },
    { header: "PV", key: "PV" },
    { header: "EV", key: "EV" },
    { header: "AC", key: "AC" },
    { header: "SPI", key: "SPI" },
    { header: "CPI", key: "CPI" },
    { header: "SV", key: "SV" },
    { header: "CV", key: "CV" },
    // { header: "Plan Effort", key: "P.Effort" },
    // { header: "Actual Effort", key: "A.Effort" },
    // { header: "% Plan Complete", key: "planComplete" },
    // { header: "% Actual Complete", key: "%A.Completed" },
    { header: "Performance", key: "performance" },
  ];

  const colorConfig = {
    // AC: {
    //   red: (value, row) => value > row["PV"],
    //   green: (value, row) => value <= row["PV"],
    // },
    // EV: {
    //   red: (value, row) => value < row["PV"],
    //   green: (value, row) => value >= row["PV"],
    // },
    SPI: {
      red: (value) => value < 1,
      green: (value) => value >= 1,
    },
    CPI: {
      red: (value) => value < 1,
      green: (value) => value >= 1,
    },
    SV: {
      red: (value) => value < 0,
      green: (value) => value >= 0,
    },
    CV: {
      red: (value) => value < 0,
      green: (value) => value >= 0,
    },
  };

  if (error) {
    return <div className="alert alert-danger">{error}</div>;
  }
  return (
    <div>
      <div className="input-group mb-3">
        <span className="input-group-text" id="basic-addon1">
          Account:
        </span>
        <input
          type="text"
          className="form-control"
          placeholder="Input Account to filter"
          aria-label="Account"
          aria-describedby="basic-addon1"
          value={account}
          onChange={(e) => setAccount(e.target.value)}
        />
      </div>
      <Table data={groupedTotals} columns={totalColumns} />
      <Table data={data} columns={columns} colorConfig={colorConfig} />
    </div>
  );
};

export default Evaluation;
