/* global console, document, Excel, Office, google */

import React, { useState, useEffect, useContext } from "react";
import PropTypes from "prop-types";
import axios from "axios";
import { Button } from "../ui/Button";
import { AppContext } from "../../MyProvider";
import Toast from "../ui/Toast.js";

const JiraTask = () => {
  const { projectInfo } = useContext(AppContext);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [tasks, setTasks] = useState([]);
  const [selectedTasks, setSelectedTasks] = useState([]);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [auth, setAuth] = useState("");
  const [toast, setToast] = useState({
    message: "",
    type: "",
    show: false,
  });

  useEffect(() => {
    if (auth !== "") {
      setIsLoggedIn(true);
      loadData();
    } else {
      setIsLoggedIn(false);
    }
  }, []);

  const loadData = async () => {
    const dummyTasks = [
      {
        id: 1,
        summary: "Task 1",
        product: "Product A",
        percentDone: "50%",
        plannedStart: "2023-10-01",
        dueDate: "2023-10-10",
        originalEstimate: "5h",
        remainingEstimate: "2h",
        assignee: "User A",
        reporter: "User B",
      },
      {
        id: 2,
        summary: "Task 2",
        product: "Product B",
        percentDone: "30%",
        plannedStart: "2023-10-05",
        dueDate: "2023-10-15",
        originalEstimate: "8h",
        remainingEstimate: "5h",
        assignee: "User C",
        reporter: "User D",
      },
      {
        id: 3,
        summary: "Task 3",
        product: "Product C",
        percentDone: "70%",
        plannedStart: "2023-10-07",
        dueDate: "2023-10-20",
        originalEstimate: "10h",
        remainingEstimate: "3h",
        assignee: "User E",
        reporter: "User F",
      },
    ];
    setTasks(dummyTasks);
  };

  async function accessJira(token) {
    try {
      console.log(". Trying to Login...");

      const response = await axios.get(projectInfo.jiraLoginURL, {
        headers: {
          Authorization: `Bearer ${token}`, // Dng Bearer token thay v username/password
          "Content-Type": "application/json",
          "User-Agent": "Mozilla/5.0 (Windows NT 10.0; Win64; x64)",
        },
      });

      if (response.status === 200) {
        console.log(". Login successful.");
        setAuth(`Bearer ${token}`);
        setToast({
          message: "Login successful. Welcome " + username,
          type: "success",
          show: true,
        });
        return true;
      } else {
        setToast({
          message: "Login Fail! Please check username and password",
          type: "success",
          show: true,
        });
        return false;
      }
    } catch (error) {
      console.error("Error logging in to Jira:", error);
      setToast({
        message: "Error logging in to Jira:" + error,
        type: "error",
        show: true,
      });
      return false;
    }
  }

  const handleTaskSelect = (taskId) => {
    setSelectedTasks((prevSelectedTasks) => {
      if (prevSelectedTasks.includes(taskId)) {
        return prevSelectedTasks.filter((id) => id !== taskId);
      } else {
        return [...prevSelectedTasks, taskId];
      }
    });
  };

  const handleCreateTasks = () => {
    return;
  };

  const handleLoginSubmit = async (event) => {
    event.preventDefault();
    const success = await accessJira(password);
    if (!success) {
      setIsLoggedIn(false);
      return;
    } else {
      setIsLoggedIn(true);
      loadData();
    }
  };

  const toggleToast = () => {
    setToast({
      message: "",
      type: "",
      show: !toast.show,
    });
  };

  if (!isLoggedIn) {
    return (
      <div>
        <h2>Login Jira</h2>
        <form onSubmit={handleLoginSubmit}>
          <input
            type="text"
            name="username"
            placeholder="Username"
            required
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
          <input
            type="password"
            name="password"
            placeholder="Password"
            required
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <button type="submit">Login</button>
        </form>
        <Toast message={toast.message} type={toast.type} show={toast.show} onClose={toggleToast} />
      </div>
    );
  }

  return (
    <div>
      <h2>Task list not yet created on Jira.</h2>
      <table className="table table-bordered">
        <thead>
          <tr>
            <th>Sel</th>
            <th>Summary</th>
            <th>Product</th>
            <th>PercentDone</th>
            <th>Planned Start</th>
            <th>Due Date</th>
            <th>Original Estimate</th>
            <th>Remaining Estimate</th>
            <th>Assignee</th>
            <th>Reporter</th>
          </tr>
        </thead>
        <tbody>
          {tasks.map((task) => (
            <tr key={task.id}>
              <td>
                <input
                  type="checkbox"
                  checked={selectedTasks.includes(task.id)}
                  onChange={() => handleTaskSelect(task.id)}
                />
              </td>
              <td>{task.summary}</td>
              <td>{task.product}</td>
              <td>{task.percentDone}</td>
              <td>{task.plannedStart}</td>
              <td>{task.dueDate}</td>
              <td>{task.originalEstimate}</td>
              <td>{task.remainingEstimate}</td>
              <td>{task.assignee}</td>
              <td>{task.reporter}</td>
            </tr>
          ))}
        </tbody>
      </table>
      <Button label={"Create Tasks To Jira"} size={"sm"} onClick={handleCreateTasks} />
    </div>
  );
};
export default JiraTask;
