import React, { useState } from "react";
import PropTypes from "prop-types";

const AccordionItem = ({ title, num, content, isOpen, onToggle }) => {
  return (
    <div className="accordion-item">
      <h2 className="accordion-header">
        <button className={`accordion-button ${!isOpen ? "collapsed" : ""}`} type="button" onClick={onToggle}>
          {title}
          <span class="badge bg-secondary">{num}</span>
        </button>
      </h2>
      <div className={`accordion-collapse collapse ${isOpen ? "show" : ""}`}>
        <div className="accordion-body">{content}</div>
      </div>
    </div>
  );
};

AccordionItem.propTypes = {
  title: PropTypes.string.isRequired, // Tiu  ca item
  num: PropTypes.string,
  content: PropTypes.oneOfType([
    // Ni dung ca item
    PropTypes.string,
    PropTypes.node,
  ]).isRequired,
  isOpen: PropTypes.bool.isRequired, // Xc nh item c m khng
  onToggle: PropTypes.func.isRequired, // Hm x l m/ng
};

const Accordion = ({ items }) => {
  const [openIndex, setOpenIndex] = useState(null);

  const handleToggle = (index) => {
    setOpenIndex(index === openIndex ? null : index);
  };

  return (
    <div className="accordion">
      {items.map((item, index) => (
        <AccordionItem
          key={index}
          title={item.title}
          num={item.num ? item.num : ""}
          content={item.content}
          isOpen={openIndex === index}
          onToggle={() => handleToggle(index)}
        />
      ))}
    </div>
  );
};

Accordion.propTypes = {
  items: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
      num: PropTypes.string,
      content: PropTypes.oneOfType([PropTypes.string, PropTypes.node]).isRequired,
    })
  ).isRequired,
};

export default Accordion;
