/* global console, document, Excel, Office, google */

import React, { createContext, useState, useEffect } from "react";

// To mt Context
export const AppContext = createContext();

export const AppProvider = ({ children }) => {
  // Cc gi tr khc nhau
  const [projectInfo, setProjectInfo] = useState({
    projectKey: "",
    projectCode: "",
    jiraBaseURL: "",
    jiraLoginURL: "",
    jiraCreateIssueURL: "",
  });

  const getProjectInfo = async () => {
    try {
      await Excel.run(async (context) => {
        // Lay worksheet 'Setting'
        const sheet = context.workbook.worksheets.getItem("Setting");

        // Lay gia tri t cell B4
        const cell = sheet.getRange("B1:B10");
        cell.load("values");

        await context.sync();

        const projectCode = cell.values[0][0];
        const customDomain = cell.values[2][0];
        const jiraBaseURL = cell.values[3][0];
        console.log("Jira Base URL:", jiraBaseURL);

        setProjectInfo({
          projectCode: projectCode,
          customDomain: customDomain,
          jiraBaseURL: jiraBaseURL,
          jiraLoginURL: `${jiraBaseURL}/rest/auth/1/session`,
          jiraCreateIssueURL: `${jiraBaseURL}/rest/api/2/issue/`,
          jiraCreateIssuesURL: `${jiraBaseURL}/rest/api/2/issue/bulk`,
        });
      });
    } catch (error) {
      console.error("Error getting Jira base URL:", error);
    }
  };

  useEffect(() => {
    getProjectInfo();
  }, []);
  return <AppContext.Provider value={{ projectInfo }}>{children}</AppContext.Provider>;
};
