import React, { Suspense, lazy } from "react";
import Home from "./Home";
const Task = lazy(() => import("./Task"));
import Evaluation from "./Evaluation";
import Monitoring from "./Monitoring";
import Importing from "./Importing";
const ProjectWorkLog = lazy(() => import("./ProjectWorkLog"));
import JiraTask from "./JiraTask";
import { AppProvider } from "../../MyProvider";

const Main = ({ activeComponent }) => {
  const renderComponent = () => {
    switch (activeComponent) {
      case "Home":
        return <Home />;
      case "Task":
        return (
          <AppProvider>
            <Suspense fallback={<div>Loading...</div>}>
              <Task />
            </Suspense>
          </AppProvider>
        );
      case "Evaluation":
        return <Evaluation />;
      case "Monitoring":
        return <Monitoring />;
      case "Importing":
        return <Importing />;
      case "ProjectWorkLog":
        return (
          <Suspense fallback={<div>Loading...</div>}>
            <ProjectWorkLog />
          </Suspense>
        );
      case "JiraTask":
        return (
          <AppProvider>
            <JiraTask />
          </AppProvider>
        );
      default:
        return <Task />;
    }
  };

  return <main>{renderComponent()}</main>;
};

export default Main;
