import React, { useEffect } from "react";
import PropTypes from "prop-types";
import { Tooltip } from "bootstrap";

const Button = ({ label, type, size, onClick, disabled }) => {
  // Xc nh cc class cho button da trn props
  const buttonClass = `btn btn-${type} btn-${size}`;

  return (
    <button type="button" className={buttonClass} onClick={onClick} disabled={disabled}>
      {label}
    </button>
  );
};

// PropTypes  m bo cc props truyn vo ng kiu
Button.propTypes = {
  label: PropTypes.string.isRequired, // Ni dung hin th trn button
  type: PropTypes.string, // Loi button (primary, secondary, v.v.)
  size: PropTypes.string, // Kch thc button (sm, lg, hoc khng c)
  onClick: PropTypes.func, // Hm x l khi click button
  disabled: PropTypes.bool, // C b disabled hay khng
};

// Gi tr mc nh cho props
Button.defaultProps = {
  type: "primary", // Loi mc nh l "primary"
  size: "", // Kch thc mc nh (khng c)
  onClick: () => {}, // Hm x l click mc nh (khng lm g)
  disabled: false, // Mc nh khng b disabled
};

const ButtonWithIcon = ({ label, type, size, icon, onClick, disabled, title }) => {
  // Xc nh cc class cho button da trn props
  const buttonClass = `btn btn-${type} btn-${size}`;

  //   useEffect(() => {
  //     // Khi to tooltip sau khi component c render
  //     const tooltipTriggerList = document.querySelectorAll('[data-bs-toggle="tooltip"]');
  //     tooltipTriggerList.forEach((tooltipTriggerEl) => {
  //       new Tooltip(tooltipTriggerEl); // Khi to tooltip cho tng phn t
  //     });
  //   }, []); // [] ch chy mt ln khi component c mount

  return (
    <button
      type="button"
      className={buttonClass}
      onClick={onClick}
      disabled={disabled}
      data-bs-toggle="tooltip"
      data-bs-placement="top"
      title={title}
    >
      {icon && <i className={`bi-${icon} me-2`}></i>} {/* Hin th icon nu c */}
      {label}
    </button>
  );
};

// PropTypes  m bo cc props truyn vo ng kiu
ButtonWithIcon.propTypes = {
  label: PropTypes.string.isRequired, // Ni dung hin th trn button
  type: PropTypes.string, // Loi button (primary, secondary, v.v.)
  size: PropTypes.string, // Kch thc button (sm, lg, hoc khng c)
  icon: PropTypes.string, // Tn ca icon (Bootstrap Icons)
  onClick: PropTypes.func, // Hm x l khi click button
  disabled: PropTypes.bool, // C b disabled hay khng
  title: PropTypes.string, // D liu lin quan n button
};

// Gi tr mc nh cho props
ButtonWithIcon.defaultProps = {
  type: "primary", // Mc nh l button "primary"
  size: "", // Khng c kch thc mc nh (c th l 'sm' hoc 'lg')
  icon: "", // Khng c icon mc nh
  onClick: () => {}, // Hm x l mc nh l khng lm g
  disabled: false, // Mc nh khng disabled
};

const ButtonWithBadge = ({ label, type, size, active, onClick, disabled, badge }) => {
  // Xc nh cc class cho button da trn props
  const buttonClass = `btn btn-${type} btn-${size} ${active}`;

  return (
    <button type="button" className={buttonClass} onClick={onClick} disabled={disabled}>
      {label} {badge && <span className="badge text-bg-secondary">{badge}</span>}
    </button>
  );
};

// PropTypes  m bo cc props truyn vo ng kiu
ButtonWithBadge.propTypes = {
  label: PropTypes.string.isRequired, // Ni dung hin th trn button
  type: PropTypes.string, // Loi button (primary, secondary, v.v.)
  size: PropTypes.string, // Kch thc button (sm, lg, hoc khng c)
  active: PropTypes.string,
  onClick: PropTypes.func, // Hm x l khi click button
  disabled: PropTypes.bool, // C b disabled hay khng
  badge: PropTypes.oneOfType([PropTypes.string, PropTypes.number]), // Ni dung hin th trn badge
};

// Gi tr mc nh cho props
ButtonWithBadge.defaultProps = {
  type: "primary", // Loi mc nh l "primary"
  size: "", // Kch thc mc nh (khng c)
  onClick: () => {}, // Hm x l click mc nh (khng lm g)
  disabled: false, // Mc nh khng b disabled
  badge: null, // Mc nh khng c badge
};

export { Button, ButtonWithIcon, ButtonWithBadge };
