import React, { useEffect, useState } from "react";
import { getDataTable, convertToJSON, excelDateToJSDate } from "../../js/excelFunction.js";
import Table from "../ui/Table.js";
import RadioButton from "../ui/RadioButton.js";
import InputGroup from "../ui/InputGroup.js";

const Monitoring = () => {
  const [data, setData] = useState([]);
  const [selectedOption, setSelectedOption] = useState("Resource Idle");
  const [missLogworkData, setMissLogworkData] = useState([]);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [error, setError] = useState("");

  useEffect(() => {
    const fetchData = async () => {
      try {
        const dataTable = await getDataTable("Resource", "TblResource");
        const jsonData = await convertToJSON(dataTable);
        setData(jsonData);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };
    fetchData();
  }, []);

  const handleLoad = async () => {
    if (!startDate) {
      setError("Please input start date.");
      return;
    }

    const start = new Date(startDate);
    const end = endDate ? new Date(endDate) : new Date();

    try {
      const timeSheetTable = await getDataTable("TimeSheet", "TblTimesheet");
      const timeSheetData = await convertToJSON(timeSheetTable);

      const accounts = data.map((item) => item.Account);
      const missingLogwork = [];

      accounts.forEach((account) => {
        let currentDate = new Date(start);
        while (currentDate <= end) {
          const dayOfWeek = currentDate.getDay();
          if (dayOfWeek === 0 || dayOfWeek === 6) {
            currentDate.setDate(currentDate.getDate() + 1);
            continue;
          }
          const dateStr = currentDate.toISOString().split("T")[0];
          const hasLogwork = timeSheetData.some(
            (entry) => entry.Account === account && excelDateToJSDate(entry.Date) === dateStr
          );
          if (!hasLogwork) {
            const existingEntry = missingLogwork.find((entry) => entry.Account === account);
            if (existingEntry) {
              existingEntry.Date += `; ${dateStr}`;
            } else {
              missingLogwork.push({
                Account: account,
                Date: dateStr,
              });
            }
          }
          currentDate.setDate(currentDate.getDate() + 1);
        }
      });

      setMissLogworkData(missingLogwork);
      setError("");
    } catch (error) {
      console.error("Error fetching timesheet data:", error);
    }
  };

  const columns = [
    { header: "Account", key: "Account" },
    { header: "Full Name", key: "Fullname" },
  ];

  const missLogworkColumns = [
    { header: "Account", key: "Account" },
    { header: "Date", key: "Date" },
  ];

  const handleOptionChange = (event) => {
    setSelectedOption(event.target.value);
  };

  return (
    <div>
      <div className="mb-3">
        <RadioButton
          name="monitoringOptions"
          value="Resource Idle"
          checked={selectedOption === "Resource Idle"}
          onChange={handleOptionChange}
          label="Resource Idle"
        />
        <RadioButton
          name="monitoringOptions"
          value="Miss Logwork"
          checked={selectedOption === "Miss Logwork"}
          onChange={handleOptionChange}
          label="Miss Logwork"
        />
      </div>
      {selectedOption === "Miss Logwork" && (
        <div className="mb-3">
          <InputGroup
            label="Start Date"
            type="date"
            value={startDate}
            onChange={(e) => setStartDate(e.target.value)}
            id="startDate"
          />
          <InputGroup
            label="End Date"
            type="date"
            value={endDate}
            onChange={(e) => setEndDate(e.target.value)}
            id="endDate"
          />
          {error && <div className="alert alert-danger">{error}</div>}
          <button className="btn btn-primary" onClick={handleLoad}>
            Load
          </button>
        </div>
      )}
      {selectedOption === "Resource Idle" && <Table data={data} columns={columns} />}
      {selectedOption === "Miss Logwork" && <Table data={missLogworkData} columns={missLogworkColumns} />}
    </div>
  );
};

export default Monitoring;
