import React, { useEffect, useState } from "react";
import { getDataTable, convertToJSON } from "../../js/excelFunction.js";
import Table from "../ui/Table.js";
import Accordion from "../ui/Accordion.js";

const Home = () => {
  const [data, setData] = useState([]);
  const [total, setTotal] = useState("0");
  const [error, setError] = useState("");

  const groupDataByKey = (data) => {
    const groups = {
      ProjectInformation: [],
      ProjectRule: [],
      ProjectMail: [],
      ProjectReport: [],
      ProjectProcess: [],
    };

    data.forEach((item) => {
      switch (item["Key"]) {
        case "PJ_Infor":
          groups.ProjectInformation.push(item);
          break;
        case "PJ_Rule":
          groups.ProjectRule.push(item);
          break;
        case "PJ_Mail":
          groups.ProjectMail.push(item);
          break;
        case "PJ_Report":
          groups.ProjectReport.push(item);
          break;
        case "PJ_Process":
          groups.ProjectProcess.push(item);
          break;
        default:
          break;
      }
    });

    return groups;
  };

  const processData = (jsonData) => {
    return jsonData.map((item) => {
      return {
        ...item,
      };
    });
  };

  useEffect(() => {
    const loadData = async () => {
      try {
        const result = await getDataTable("Setting", "TblMaster");
        const jsonData = convertToJSON(result);
        const updatedData = processData(jsonData);
        setData(updatedData);
        setTotal(updatedData.length);
      } catch (error) {
        setError("File hin ti cha c cu hnh ph hp  s dng plugin.");
        console.error("Error loading data:", error);
      }
    };

    loadData();
  }, []);

  const columns = [
    { header: "Label", key: "Label" },
    { header: "Text", key: "Text" },
  ];

  const groupedData = groupDataByKey(data);

  const accordionItems = [
    {
      title: " Project Information",
      content: <Table data={groupedData.ProjectInformation} columns={columns} />,
    },
    {
      title: " Project Rule",
      content: <Table data={groupedData.ProjectRule} columns={columns} />,
    },
    {
      title: " Project Mail",
      content: <Table data={groupedData.ProjectMail} columns={columns} />,
    },
    {
      title: " Project Report",
      content: <Table data={groupedData.ProjectReport} columns={columns} />,
    },
    {
      title: "Project Process",
      content: <Table data={groupedData.ProjectProcess} columns={columns} />,
    },
  ];

  // Hin th thng bo li nu c li
  if (error) {
    return <div className="alert alert-danger">{error}</div>;
  }
  return (
    <div>
      <Accordion items={accordionItems} />
    </div>
  );
};

export default Home;
