import React from "react";
import PropTypes from "prop-types";

const Table = ({ data, columns, colorConfig, projectInfo }) => {
  if (!data || data.length === 0) {
    return <p>No data available</p>; // Hien thi neu khong co du lieu
  }

  const getBackgroundColor = (columnKey, value, row) => {
    if (colorConfig && colorConfig[columnKey]) {
      const { red, green } = colorConfig[columnKey];
      if (red && red(value, row)) return "red";
      if (green && green(value, row)) return "green";
    }
    return "transparent"; // Mau nen mac inh
  };

  const getTextColor = (columnKey, value, row) => {
    if (colorConfig && colorConfig[columnKey]) {
      const { textWhite } = colorConfig[columnKey];
      if (textWhite && textWhite(value, row)) return "white";
    }
    return "black"; // Mau text mac inh
  };

  return (
    <div style={{ maxHeight: "500px", overflowY: "auto" }}>
      <table className="table table-bordered">
        <thead>
          <tr>
            {/* Render cac tieu e tu prop columns */}
            {Array.isArray(columns) && columns.map((column) => <th key={column.key}>{column.header}</th>)}
          </tr>
        </thead>
        <tbody>
          {/* Render tung hang du lieu */}
          {Array.isArray(data) &&
            data.map((row, rowIndex) => (
              <tr key={rowIndex}>
                {Array.isArray(columns) &&
                  columns.map((column) => (
                    <td
                      key={column.key}
                      style={{
                        backgroundColor: getBackgroundColor(column.key, row[column.key], row),
                        color: getTextColor(column.key, row[column.key], row),
                      }}
                    >
                      {column.key === "TaskName" ? (
                        <span
                          className="d-inline-block text-truncate"
                          style={{ maxWidth: "50px" }}
                          title={row[column.key]}
                        >
                          {row[column.key]}
                        </span>
                      ) : column.key === "LinkJira" && projectInfo ? ( // Kim tra nu jiraInfo c c truyn vo
                        (() => {
                          const jiraBaseURL = projectInfo.jiraBaseURL; // S dng jiraInfo nu c
                          const jiraKey = row[column.key].split("/").pop(); // Ly Jira Key t URL
                          return (
                            <a
                              href={`${jiraBaseURL}/browse/${jiraKey}`}
                              target="_blank"
                              rel="noopener noreferrer"
                              title={`Open Jira issue ${jiraKey}`}
                            >
                              {jiraKey ? "Link" : ""}
                            </a>
                          );
                        })()
                      ) : column.key === "TaskID" && projectInfo ? ( // Kim tra nu jiraInfo c c truyn vo
                        (() => {
                          const customDomain = projectInfo.customDomain; // S dng jiraInfo nu c
                          const taskId = row[column.key].split("/").pop(); // Ly Jira Key t URL
                          return (
                            <a href={`${customDomain}/${taskId}`} target="_blank" rel="noopener noreferrer">
                              {taskId}
                            </a>
                          );
                        })()
                      ) : (
                        row[column.key]
                      )}
                    </td>
                  ))}
              </tr>
            ))}
        </tbody>
      </table>
    </div>
  );
};

// Xac inh kieu du lieu cho component Table
Table.propTypes = {
  data: PropTypes.arrayOf(PropTypes.object).isRequired, // Du lieu la mot mang object JSON
  columns: PropTypes.arrayOf(
    PropTypes.shape({
      header: PropTypes.string.isRequired, // Ten cot hien thi
      key: PropTypes.string.isRequired, // Key trong du lieu JSON
    })
  ).isRequired,
  colorConfig: PropTypes.object, // Cau hinh mau sac cho cac cot
  projectInfo: PropTypes.object,
};

export default Table;
